import { __ } from "@wordpress/i18n";
import { useState, useEffect, useRef } from "@wordpress/element";
import { Placeholder, SelectControl } from "@wordpress/components";
import { InspectorControls, RichText, useBlockProps } from '@wordpress/block-editor';
import ServerSideRender from "@wordpress/server-side-render";
import Inspector from "./inspector";
import './editor.scss';

export default function Edit({ attributes, setAttributes, clientId }) {
	const {
		blockID,
		deviceType,
		formId,
		showLabels,
		showPlaceholder,
		showErrorMessage,

		fluentFormGlobalWraperID,
		fluentFormGlobalWraperClass,

		fluentFormDesktopSwitcher,
		fluentFormTabletSwitcher,
		fluentFormMobileSwitcher,
	} = attributes;

	const blockProps = useBlockProps({
		className: [
			'fb-parent-wrapper fb-fluent-form-wrapper',
			blockID,
			`${fluentFormDesktopSwitcher && 'fb-desktop-responsive'} ${fluentFormTabletSwitcher && 'fb-tablet-responsive'} ${fluentFormMobileSwitcher && 'fb-mobile-responsive'}`,
			!showLabels && 'fb-fluent-form-hide-label',
			!showPlaceholder && 'fb-fluent-form-hide-placeholder',
			!showErrorMessage && 'fb-fluent-form-hide-error-message',
		].filter(Boolean).join(' '),
	});

	const formLists = JSON.parse(FrontisBlocksData.fluent_form_lists);


	const modifiedFormLists = formLists.map((obj) => {
		return {
			label: obj.label,
			value: obj.value,
		};
	});

	const pluginInstallUrl = FrontisBlocksData.fluent_form_install_url;

	return (
		<>
			<Inspector
				attributes={attributes}
				setAttributes={setAttributes}
				clientId={clientId}
			/>

			<div {...blockProps}>
				{!FrontisBlocksData.fluent_plugin_active ? (
					<div className="fb-reusable_notice" id={fluentFormGlobalWraperID}>
						<h4 className="title">
							<svg xmlns="http://www.w3.org/2000/svg" width="66" height="70" fill="none" viewBox="0 0 66 70">
								<path fill="#399CFF" d="M46.161 30H12.84c-1.15 0-1.839.91-1.839 1.818v11.364c0 1.136.92 1.818 1.838 1.818h33.323c1.15 0 1.839-.91 1.839-1.818V31.818C48 30.91 47.31 30 46.161 30zm0 13.41H12.84V32.045H46.16v11.363z"></path>
								<path fill="#399CFF" d="M58.087 6H7.913C5.61 6 4 7.841 4 9.913v50.174C4 62.39 5.841 64 7.913 64h50.174C60.39 64 62 62.159 62 60.087V9.913C62 7.61 60.159 6 58.087 6zM6.071 13.595V9.913c0-.92.921-1.842 1.842-1.842h50.174c.92 0 1.842.921 1.842 1.842v3.682H6.07zm0 2.072H59.93v44.19c0 .92-.921 1.841-1.842 1.841H7.913c-.92 0-1.842-.92-1.842-1.84V15.666z"></path>
								<path fill="#399CFF" d="M9.929 10H9.07C8.43 10 8 10.4 8 11s.429 1 1.071 1h.858C10.57 12 11 11.6 11 11s-.429-1-1.071-1zM14.929 10h-.858C13.43 10 13 10.4 13 11s.429 1 1.071 1h.858C15.57 12 16 11.6 16 11s-.429-1-1.071-1zM19.571 10H18.43C17.57 10 17 10.4 17 11s.571 1 1.429 1h1.142C20.43 12 21 11.6 21 11s-.571-1-1.429-1zM12.154 24h33.692C46.54 24 47 23.6 47 23s-.462-1-1.154-1H12.154C11.46 22 11 22.4 11 23s.692 1 1.154 1zM22.929 53H13.07C12.43 53 12 53.4 12 54s.429 1 1.071 1h9.858C23.57 55 24 54.6 24 54s-.429-1-1.071-1z"></path>
							</svg>
							Fluent Forms
						</h4>
						<p className="content">
							<strong className="fb-reusable-notice-text">Fluent Form</strong> is not installed/activated on your site. Please install and activate
							<a href={pluginInstallUrl} target="_blank"> Fluent Form</a> first.
						</p>
					</div>
				) : (
					<>
						<div className={fluentFormGlobalWraperClass} id={fluentFormGlobalWraperID}>
							{!formId && (
								<SelectControl
									label={__('Fluent Form', 'frontis-blocks')}
									value={formId}
									options={modifiedFormLists}
									onChange={(newFormId) =>
										setAttributes({ formId: newFormId })
									}
									className="fb-fluent-form-select"
								/>
							)}

							{formId && (
								<ServerSideRender
									className="fb-fluent-form-rendered"
									block="fluentfom/guten-block"
									attributes={{ formId: formId }}
								/>
							)}
						</div>
					</>
				)}
			</div>
		</>
	);
}